.container {
  position: relative;
  background: rgba(0, 0, 0, .1);
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  outline: none;
}
.coverflow {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
}
.stage {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  transform-style: preserve-3d;
  perspective: 500px;
}
.figure {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  flex: 0 0 auto;
  cursor: pointer;
  transition: transform 600ms ease;
  backface-visibility: hidden;
  z-index: 9;
  align-self: center;
  box-shadow: 0 50px 70px rgba(0, 0, 0, 0.5);
 /*TODO: box-reflect crashing the app. Resolve issue*/
  -webkit-box-reflect: below 1px -webkit-linear-gradient(bottom, rgba(0, 0, 0, .6), rgba(0, 0, 0, .1) 20%, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 0));
}
.cover {
  display: block;
  width: 100%;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, .5);
}
.preloader {
  display: hidden;
}
.text {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.9em;
  color: white;
  padding: 5px;
  overflow: hidden;
  background: rgba(0, 0, 0, .6);
}
@keyframes prevent {
  0% {
    pointer-events: none;
 }
  100% {
    pointer-events: auto;
 }
}
.arrowWrapper {
  display: flex;
  align-items: center;
}
#arrow1 .arrow {
  z-index: 999;
  opacity: 1 !important;
  cursor: pointer;
  display: block;
  width: 35.360678925px;
  height: 70.7213578501px;
 /*width: $size;
 */
 /* height: $size;
 */
  position: relative;
}
#arrow1 .arrow.left {
  left: 50px;
}
#arrow1 .arrow.right {
  right: 50px;
}
#arrow1 .arrow span, #arrow1 .arrow:before, #arrow1 .arrow:after {
  background: #fff;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, .5);
  content: '';
  display: block;
  width: 50px;
  height: 2px;
  position: absolute;
  top: calc(50% - (2px / 2));
}
#arrow1 .arrow:before {
  transform: rotate(-45deg);
}
#arrow1 .arrow:after {
  transform: rotate(45deg);
}
#arrow1 .arrow span {
  width: 0;
}
#arrow1 .arrow:hover span {
  width: 70.7px;
}
#arrow1 .arrow.left span, #arrow1 .arrow.left:before, #arrow1 .arrow.left:after {
  margin: 0 12.5px;
  left: 0;
  transform-origin: left 50%;
}
#arrow1 .arrow.left:before, #arrow1 .arrow.left:after {
  transition: left 0.3s 0.05s;
}
#arrow1 .arrow.left span {
  transition: width 0.3s, left 0.3s 0.05s;
}
#arrow1 .arrow.left:hover span, #arrow1 .arrow.left:hover:before, #arrow1 .arrow.left:hover:after {
  left: -35.360678925px;
}
#arrow1 .arrow.right span, #arrow1 .arrow.right:before, #arrow1 .arrow.right:after {
  margin: 0 12.5px;
  right: 0;
  transform-origin: right 50%;
}
#arrow1 .arrow.right:before, #arrow1 .arrow.right:after {
  transition: right 0.3s 0.05s;
}
#arrow1 .arrow.right span {
  transition: width 0.3s, right 0.3s 0.05s;
}
#arrow1 .arrow.right:hover span, #arrow1 .arrow.right:hover:before, #arrow1 .arrow.right:hover:after {
  right: -35.360678925px;
}
